// ? 1.0 –≠—Ç–æ—Ç —Ñ–∞–π–ª –Ω–µ —Å–ø—Ä–æ—Å—Ç–∞ –Ω–∞–∑—ã–≤–∞–µ—Ç—Å—è filters –≤–æ –º–Ω–æ–∂. —á–∏—Å–ª–µ, —Ñ–∏–ª—å—Ç—Ä–æ–≤ –±—É–¥–µ—Ç –Ω–µ—Å–∫–æ–ª—å–∫–æ. –ü–æ—ç—Ç–æ–º—É –º—ã —ç–∫—Å–ø–æ—Ä—Ç–∏—Ä—É–µ–º –∫–∞–∂–¥—É—é —Ñ—É–Ω–∫—Ü–∏—é –∫–∞–∫ –æ–±—ã—á–Ω—É—é —Ñ—É–Ω–∫—Ü–∏—é. –¢–∞–∫–∂–µ –∏ –∏–º–ø–æ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å –º—ã –±—É–¥–µ–º —á—É—Ç–æ—á–∫—É –∏–Ω–∞—á–µ. [—Å–º. search.js] –î–µ–ª–æ –≤ —Ç–æ–º, —á—Ç–æ –∫–æ–≥–¥–∞ –º—ã —ç–∫—Å–ø–æ—Ä—Ç–∏—Ä—É–µ–º –ø–æ –¥–µ—Ñ–æ–ª—Ç—É, —Ç–æ —ç–∫—Å–ø–æ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å –∏–∑ —Ñ–∞–π–ª–∞ –º—ã –º–æ–∂–µ–º –ª–∏—à—å –æ–¥–Ω—É —Ñ—É–Ω–∫—Ü–∏—é.
// 1.1 –ë—É–¥–µ—Ç –ø—Ä–∏–Ω–∏–º–∞—Ç—å –≤–µ—Å—å –º–∞—Å—Å–∏–≤ goods –∏ value, —Å—Ç—Ä–æ–∫—É, –∫–æ—Ç–æ—Ä—É—é –º—ã –±—É–¥–µ–º –≤–≤–æ–¥–∏—Ç—å –≤ —Å—Ç—Ä–æ–∫—É –ø–æ–∏—Å–∫–∞.
// 1.2.1 –ò—Ç–∞–∫ –Ω–∞—à–∞ –∑–∞–¥–∞—á–∞ –æ—Ç—Ñ–∏–ª—å—Ç—Ä–æ–≤–∞—Ç—å –≤–µ—Å—å –º–∞—Å—Å–∏–≤ –¥–∞–Ω–Ω—ã—Ö –ø–æ –¥–∞–Ω–Ω–æ–º—É value, —Ç.–µ. –ø–æ –∑–Ω–∞—á–µ–Ω–∏—é —Å—Ç—Ä–æ–∫–∏ –≤–≤–µ–¥—ë–Ω–Ω–æ–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º –≤ –ø–æ–ª–µ –≤–≤–æ–¥–∞ –ø–æ–∏—Å–∫–∞, –æ—Å—Ç–∞–≤–∏–≤ —Ç–æ–ª—å–∫–æ —Ç–µ —ç–ª–µ–º–µ–Ω—Ç—ã, –∫–æ—Ç–æ—Ä—ã–µ –Ω–∞–º –Ω—É–∂–Ω–æ –æ—Ç–æ–±—Ä–∞–∑–∏—Ç—å –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ. –î–ª—è —ç—Ç–æ–π –∑–∞–¥–∞—á–∏ –≤–æ—Å–ø–æ–ª—å–∑—É–µ–º—Å—è –º–µ—Ç–æ–¥–æ–º filter(), –∫–æ—Ç–æ—Ä—ã–π –ø–µ—Ä–µ–±–∏—Ä–∞–µ—Ç –º–∞—Å—Å–∏–≤, —Ç–µ–º —Å–∞–º—ã–º –≤–æ–∑–≤—Ä–∞—â–∞—è –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤, –≤ –∫–æ—Ç–æ—Ä—ã–π –ø–æ–ø–∞–¥—É—Ç —ç–ª–µ–º–µ–Ω—Ç—ã, –∫–æ—Ç–æ—Ä—ã–µ —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—Ç –Ω–∞—à–∏–º —Ç—Ä–µ–±–æ–≤–∞–Ω–∏—è–º.
// 1.2.2 –ú–µ—Ç–æ–¥ filter() –±—É–¥–µ—Ç –∏–º–µ—Ç—å callback-—Ñ—É–Ω–∫—Ü–∏—é —Å –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–º goodsItem, –∫–æ—Ç–æ—Ä—ã–µ –æ–∑–Ω–∞—á–∞–µ—Ç –∫–∞–∂–¥—ã–π –∏—Ç–µ—Ä–∏—Ä—É–µ–º—ã–π —ç–ª–µ–º–µ–Ω—Ç. –í –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤ filter() –±—É–¥–µ—Ç –ø–æ–º–µ—â–∞—Ç—å —Ç–µ —ç–ª–µ–º–µ–Ω—Ç—ã, callback-—Ñ—É–Ω–∫—Ü–∏—è –∫–æ—Ç–æ—Ä—ã—Ö –±—É–¥–µ—Ç –≤–æ–∑–≤—Ä–∞—â–∞—Ç—å true –∏ –∏–≥–Ω–æ—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è —Ç–µ, —á—Ç–æ –±—É–¥—É—Ç –≤–æ–∑–≤—Ä–∞—â–∞—Ç—å false.
// 1.2.3 –ù–æ —Ç–∞–∫ –∫–∞–∫ –Ω–∞–º –Ω—É–∂–Ω–æ –∏—Å–∫–∞—Ç—å —Å–æ–≤–ø–∞–¥–µ–Ω–∏–µ —Å –≤–≤–æ–¥–æ–º –≤ –ø–æ–ª–µ –ø–æ–∏—Å–∫–∞, —Ç–æ –Ω–∞–º –ø—Ä–∏–≥–æ–¥–∏—Ç—Å—è –º–µ—Ç–æ–¥ includes(), –∫–æ—Ç–æ—Ä—ã–π –±—É–¥–µ—Ç —Å—Ä–∞–≤–Ω–∏–≤–∞—Ç—å –∑–Ω–∞—á–µ–Ω–∏–µ value —Å–æ –∑–Ω–∞—á–µ–Ω–∏–µ–º –≤ —Å–≤–æ–π—Å—Ç–≤–µ title –∫–∞–∂–¥–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞.
export const searchFilter = (goods, value) => {
  return goods.filter((goodsItem) => {
    // return goodsItem.sale === true; - –µ—Å–ª–∏ –º—ã –Ω–∞–ø—Ä–∏–º–µ—Ä –ø—Ä–æ–ø–∏—à–µ–º —Ç–∞–∫, —Ç–æ —É –Ω–∞—Å –æ—Å—Ç–∞–Ω—É—Ç—Å—è –ª–∏—à—å —Ç–µ —ç–ª–µ–º–µ–Ω—Ç—ã –º–∞—Å—Å–∏–≤–∞, —É –∫–æ—Ç–æ—Ä—ã–π —Å–≤–æ–π—Å—Ç–≤–æ sale —Å—Ç—Ä–æ–≥–æ —Ä–∞–≤–Ω—è–µ—Ç—Å—è true, —Ç.–µ. –ª–∏—à—å —Ç–æ–≤–∞—Ä—ã –ø–æ –∞–∫—Ü–∏–∏.
    return goodsItem.title.toLowerCase().includes(value.toLowerCase()); // –¥–æ–±–∞–≤–∏–º —Ç–∞–∫–∂–µ –º–µ—Ç–æ–¥ toLowerCase() –∫ –Ω–∞–∑–≤–∞–Ω–∏—é –∏ –∫ –≤–≤–µ–¥—ë–Ω–Ω–æ–º—É –∑–Ω–∞—á–µ–Ω–∏—é, —á—Ç–æ–±—ã –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏ –ø–µ—Ä–µ–≤–æ–¥–∏—Ç—å –≤–≤–µ–¥—ë–Ω–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ –≤ –Ω–∏–∂–Ω–∏–π —Ä–µ–≥–∏—Å—Ç—Ä, —á—Ç–æ–±—ã –∞—Ä—Ç–∏–∫–ª–∏ –Ω–∞—Ö–æ–¥–∏–ª–∏—Å—å –≤–Ω–µ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç —Ç–æ–≥–æ, –≤ –∫–∞–∫–æ–º —Ä–µ–≥–∏—Å—Ç—Ä–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –∏—Ö –∏—Å–∫–∞–ª.
  });
};
// 2.1 –ó–¥–µ—Å—å –º—ã –≤–æ–∑–≤—Ä–∞—â–∞–µ–º —Ç–æ—Ç –º–∞—Å—Å–∏–≤, —ç–ª–µ–º–µ–Ω—Ç—ã –∫–æ—Ç–æ—Ä–æ–≥–æ –∏–º–µ—é—Ç –æ–ø—Ä–µ–¥–µ–ª—ë–Ω–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ –∫–∞—Ç–µ–≥–æ—Ä–∏–π. –£ –∫–∞–∂–¥–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞ –µ—Å—Ç—å —Å–≤–æ–π—Å—Ç–≤–æ category –∏ –æ–Ω–æ —Ä–∞–≤–Ω–æ –æ–¥–Ω–æ–π –∏–∑ –∫–∞—Ç–µ–≥–æ—Ä–∏–π, –ø–æ –∫–æ—Ç–æ—Ä–æ–π –º—ã –º–æ–∂–µ–º –∫–ª–∏–∫–Ω—É—Ç—å –≤ –º–æ–¥–∞–ª—å–Ω–æ–º –æ–∫–Ω–µ ¬´–ö–∞—Ç–∞–ª–æ–≥¬ª [—Å–º. catalogue.js], –∞ —Ç–µ–∫—Å—Ç —Å –Ω–∞–∑–≤–∞–Ω–∏–µ–º –∫–∞—Ç–µ–≥–æ—Ä–∏–∏ –º—ã –ø–µ—Ä–µ–¥–∞—ë–º –≤ value.
export const categoryFilter = (goods, value) => {
  return goods.filter((goodsItem) => {
    return goodsItem.category === value;
  });
};
// 3.1 –ó–¥–µ—Å—å –Ω–∞–º –Ω—É–∂–Ω–æ –≤–æ–∑–≤—Ä–∞—â–∞—Ç—å –≤—Å–µ —ç–ª–µ–º–µ–Ω—Ç—ã —É –∫–æ—Ç–æ—Ä—ã—Ö –∑–Ω–∞—á–µ–Ω–∏–µ —Å–≤–æ–π—Å—Ç–≤–∞ price >=, —á–µ–º minValue. –ù–æ —Ç–∞–∫ –∫–∞–∫ minValue —É –Ω–∞—Å –ø—Ä–∏—Ö–æ–¥–∏—Ç –∏–∑ input, –∞ –∑–Ω–∞—á–∏—Ç —è–≤–ª—è–µ—Ç—Å—è —Å—Ç—Ä–æ–∫–æ–π, —Ç–æ –Ω–∞–º —Å–ø–µ—Ä–≤–∞ –Ω—É–∂–Ω–æ –∫–æ–Ω–≤–µ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å –µ–≥–æ –≤ —á–∏—Å–ª–æ–≤–æ–π —Ç–∏–ø –¥–∞–Ω–Ω—ã—Ö –ø—Ä–∏ –ø–æ–º–æ—â–∏ parseInt(). –¢–æ–∂–µ —Å–∞–º–æ–µ, —Ç–æ–ª—å–∫–æ <= –±—É–¥–µ—Ç —É –Ω–∞—Å —Å maxValue.
// 3.2 –ù–æ —Ç–∞–∫ –∫–∞–∫ –ø—Ä–æ–±–µ–ª—ã –≤—ã–¥–∞—é—Ç –Ω–∞–º false, —Ç–æ –ª—É—á—à–µ –Ω–∞–º —ç—Ç–æ –≤—Å—ë –∑–∞–≤–µ—Ä–Ω—É—Ç—å –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω—ã–µ isMin & isMax –∏ –≤–æ–∑–≤—Ä–∞—â–∞—Ç—å —É–∂–µ –∏—Ö.
// 3.5.2 –î–æ–±–∞–≤–ª—è–µ–º –∏ —Å—é–¥–∞ –ø–∞—Ä–∞–º–µ—Ç—Ä —Å—Ç–∞—Ç—É—Å–∞ —á–µ–∫–±–æ–∫—Å–∞ checkValue. –¢–∞–∫–∂–µ –∏—â–µ–º –∫ –∫–∞–∫–æ–º—É —Å–≤–æ–π—Å—Ç–≤—É goodsItem‚Äô–∞ –º–æ–∂–Ω–æ –ø—Ä–∏–≤—è–∑–∞—Ç—å –Ω–∞—à–µ —É—Å–ª–æ–≤–∏–µ –ø–æ —á–µ–∫–±–æ–∫—Å—É. –ù–∞—Ö–æ–¥–∏–º ¬´sale¬ª, –µ–≥–æ-—Ç–æ –º—ã –∏ —É–∫–∞–∂–µ–º –≤ —É—Å–ª–æ–≤–∏–∏, –Ω–æ —Ç–æ–ª—å–∫–æ –ø—Ä–∏ –Ω–∞–ª–∏—á–∏–∏ –∞–∫—Ü–∏–∏, —Ç.–µ. checkValue.
// 4.1 –¢–µ–ø–µ—Ä—å –Ω–∞–º –Ω—É–∂–Ω–æ —Ç–∞–∫–∂–µ –æ–±—ä–µ–¥–∏–Ω–∏—Ç—å –¥–≤–∞ —Ñ–∏–ª—å—Ç—Ä–∞, –ø–æ–∏—Å–∫–∞ –∏ —Ü–µ–Ω, —á—Ç–æ–±—ã –æ–Ω–∏ —Å–æ–≥–ª–∞—Å–æ–≤—ã–≤–∞–ª–∏—Å—å –º–µ–∂–¥—É —Å–æ–±–æ–π. –î–ª—è —ç—Ç–æ–≥–æ –¥–æ–±–∞–≤–∏–º searchStr
export const funcFilter = (
  goods,
  minValue,
  maxValue,
  checkValue,
  searchValue
) => {
  return goods.filter((goodsItem) => {
    //? –ö—Å—Ç–∞—Ç–∏ –Ω–∏–∂–µ –∑–∞–ø–∏—Å–∞–Ω–Ω—É—é —Ñ—É–Ω–∫—Ü–∏—é –º–æ–∂–Ω–æ –±—ã–ª–æ —É–ø—Ä–æ—Å—Ç–∏—Ç—å.
    const isMin = minValue.trim()
      ? goodsItem.price >= parseInt(minValue.trim())
      : true;
    const isMax = maxValue.trim()
      ? goodsItem.price <= parseInt(maxValue.trim())
      : true;
    const isSale = checkValue ? goodsItem.sale : true;

    return (
      isMin &&
      isMax &&
      isSale &&
      goodsItem.title.toLowerCase().includes(searchValue.toLowerCase())
    );
  });
};

// ? === –ê–ª—å—Ç–µ—Ä–Ω–∞—Ç–∏–≤–Ω–æ–µ —Ä–µ—à–µ–Ω–∏–µ priceFilter() —Ñ—É–Ω–∫—Ü–∏–∏ –∏–∑ —É—Ä–æ–∫–∞ ‚Ññ4 ===
export const priceFilter = (goods, min, max) => {
  return goods.filter((goodsItem) => {
    if (min === '' && max === '') {
      return goodsItem;
    } else if (min !== '' && max !== '') {
      return goodsItem.price > +min && goodsItem.price < +max;
    } else if (min !== '' && max === '') {
      return goodsItem.price > +min;
    } else if (min === '' && max !== '') {
      return goodsItem.price < +max;
    }
  });
};

export const hotSaleFilter = (goods, value) => {
  return goods.filter((goodsItem) => {
    if (value) {
      return goodsItem.sale === true;
    } else {
      return goodsItem;
    }
  });
};
