import getData from './get-data.js';
import renderGoods from './render-goods.js';
import { categoryFilter } from './filters.js';
// 1.0 –†–µ–∞–ª–∏–∑—É–µ–º –ø–æ –∫–ª–∏–∫—É –Ω–∞ –∫–Ω–æ–ø–∫–µ "–ö–∞—Ç–∞–ª–æ–≥" –∏ –≤—ã–±–æ—Ä–µ –Ω—É–∂–Ω–æ–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—é –∫–∞—Ç–µ–≥–æ—Ä–∏–∏, —Ñ–∏–ª—å—Ç—Ä–∞—Ü–∏—é –≤—Å–µ–≥–æ —Ç–æ–≤–∞—Ä–∞ –ø–æ –∫–∞—Ç–µ–≥–æ—Ä–∏—è–º —ç—Ç–æ–≥–æ —Ç–æ–≤–∞—Ä–∞.
const catalogue = () => {
  // 1.1 –°–ø–µ—Ä–≤–∞ –ø–æ–ª—É—á–∏–º —Å–∞–º—É –∫–Ω–æ–ø–∫—É –∫–Ω–æ–ø–∫–∏ "–ö–∞—Ç–∞–ª–æ–≥".
  const catalogueButton = document.querySelector('.catalog-button > button');
  const catalogueModal = document.querySelector('.catalog');
  // 1.3.1 –¢–∞–∫–∂–µ –Ω–∞–º –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –ø–æ–ª—É—á–∏—Ç—å –Ω–∞—à —Å–ø–∏—Å–æ–∫ –∫–∞—Ç–µ–≥–æ—Ä–∏–π. –≠—Ç–æ li‚Äô—à–∫–∏ —Å –∫–∞–∫–∏–º-—Ç–æ —Ç–µ–∫—Å—Ç–æ–º –≤–Ω—É—Ç—Ä–∏ —Å–µ–±—è.
  const catalogueCategories = document.querySelectorAll('.catalog li');
  // 1.2.1 –ü–æ–≤–µ—Å–∏–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è –Ω–∞ —ç—Ç—É –∫–Ω–æ–ø–∫—É. –ò –ø–æ –∫–ª–∏–∫—É –Ω–∞ –Ω–µ—ë –Ω–∞–º –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –ø–æ–∫–∞–∑—ã–≤–∞—Ç—å –º–∞–ª–µ–Ω—å–∫–æ–µ –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ.
  // 1.2.2 –¢–∞–∫–∂–µ –Ω–∞–º –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –∑–∞–∫—Ä—ã–≤–∞—Ç—å –¥–∞–Ω–Ω–æ–µ –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ.
  // ? –û–±—ã—á–Ω–æ –∑–¥–µ—Å—å –¥–µ–ª–∞—é—Ç –∫–∞–∫–æ–π-—Ç–æ –∫–ª–∞—Å—Å –∏ –¥–æ–±–∞–≤–ª—è—é—Ç –µ–≥–æ –ø—Ä–∏ –ø–æ–º–æ—â–∏ toggle(), –Ω–æ –¥–∞–Ω–Ω—ã–π –∫–ª–∞—Å—Å –Ω–µ –ø—Ä–µ–¥—É—Å–º–æ—Ç—Ä–µ–Ω –≤—ë—Ä—Å—Ç–∫–æ–π, –∏ —Ç.–∫. –º—ã —Ä–∞–±–æ—Ç–∞–µ–º –Ω–∞ —ç—Ç–æ–º –∏–Ω—Ç–µ–Ω—Å–∏–≤–µ —Å JS, —Ç–æ –∏—Å–ø–æ–ª—å–∑—É–µ–º –∑–¥–µ—Å—å –Ω–µ—Å–∫–æ–ª—å–∫–æ –∫–æ—Å—Ç—ã–ª—å–Ω—ã–π —Å–ø–æ—Å–æ–±, –∫–æ—Ç–æ—Ä—ã–π –æ–¥–Ω–∞–∫–æ –º–æ–∂–µ—Ç –ø—Ä–∏–≥–æ–¥–∏—Ç—Å—è –≥–¥–µ-–Ω–∏–±—É–¥—å –µ—â—ë.
  // 1.2.3 –°–æ–∑–¥–∞–¥–∏–º —Ç–µ—Ö–Ω–∏—á–µ—Å–∫—É—é –∏–∑–º–µ–Ω—è–µ–º—É—é –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é isOpen...
  let isOpen = false;

  catalogueButton.addEventListener('click', () => {
    // 1.2.4 ...–∏ –ø–æ –∫–∞–∂–¥–æ–º—É –∫–ª–∏–∫—É –º—ã –±—É–¥–µ–º –º–µ–Ω—è—Ç—å –∑–Ω–∞—á–µ–Ω–∏–µ —ç—Ç–æ–π –ø–µ—Ä–µ–º–µ–Ω–Ω–æ–π –Ω–∞ –ø—Ä–æ—Ç–∏–≤–æ–ø–æ–ª–æ–∂–Ω–æ–µ.
    isOpen = !isOpen;
    // 1.2.5 –î–∞–ª–µ–µ –º—ã –±—É–¥–µ–º –ø—Ä–æ–≤–µ—Ä—è—Ç—å, –µ—Å–ª–∏ isOpen —Ä–∞–≤–µ–Ω true, —Ç–æ–≥–¥–∞ –º—ã –±—É–¥–µ–º –¥–∞–≤–∞—Ç—å –º–æ–¥–∞–ª—å–Ω–æ–º—É –æ–∫–Ω—É —Å–≤–æ–π—Å—Ç–≤–æ display: block;, –≤ –ø—Ä–æ—Ç–∏–≤–Ω–æ–º —Å–ª—É—á–∞–µ —É–±–∏—Ä–∞—Ç—å –µ–≥–æ.
    if (isOpen) {
      catalogueModal.style.display = 'block';
    } else {
      catalogueModal.style.display = '';
    }
  });
  // 1.3.2 –¢–µ–ø–µ—Ä—å –ø–µ—Ä–µ–±–µ—Ä—ë–º –≤—Å–µ —ç—Ç–∏ —ç–ª–µ–º–µ–Ω—Ç—ã –∏ –ø–æ–≤–µ—Å–∏–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∏ —Å–æ–±—ã—Ç–∏—è. –ò –ø–æ –∫–ª–∏–∫—É –Ω–∞ –∫–∞–∂–¥—É—é –∏–∑ li‚Äô—à–µ–∫ –∫–∞—Ç–µ–≥–æ—Ä–∏–π –Ω–∞–º –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –ø–æ–ª—É—á–∏—Ç—å –∏—Ö —Ç–µ–∫—Å—Ç–æ–≤–æ–µ —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ.
  // 1.4 –¢–µ–ø–µ—Ä—å –∑–∞–¥–∞—á–∞ –ø–∞—Ä–∞–ª–ª–µ–ª—å–Ω–æ –∑–∞–ø—É—Å–∫–∞—Ç—å —Ñ–∏–ª—å—Ç—Ä–∞—Ü–∏—é –¥–∞–Ω–Ω—ã—Ö –∏ –∏—Ö –ø–æ–ª—É—á–µ–Ω–∏–µ. –î–ª—è —ç—Ç–æ–≥–æ –≤–æ–∑—å–º—ë–º –∏–∑ search.js —Ñ—É–Ω–∫—Ü–∏—é getData() –∏ –≤—ã–∑–æ–≤–µ–º –µ—ë –ø–æ –∫–ª–∏–∫—É. (–Ω–µ –∑–∞–±—ã–≤–∞–µ–º –∏–º–ø–æ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å getData() & renderGoods())
  // 1.5 –¢–∞–∫–∂–µ –Ω–∞–º —Ç—Ä–µ–±—É–µ—Ç—Å—è —Å–æ–∑–¥–∞—Ç—å –Ω–æ–≤—É—é —Ñ—É–Ω–∫—Ü–∏—é –≤ filters.js [categoryFilter()]
  catalogueCategories.forEach(category => {
    category.addEventListener('click', () => {
      const content = category.textContent;

      getData().then((data) => {
        renderGoods(categoryFilter(data, content));
      });
    });
  });
};

export default catalogue;
