import renderCart from './render-cart.js';
import postData from './post-data.js';

// import getData from './get-data.js'; // ? 0.1 - –º–æ–¥—É–ª–∏ –Ω–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –≤—Å–µ–≥–¥–∞ –∏–º–ø–æ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å –∏ –∑–∞–ø—É—Å–∫–∞—Ç—å –≤ —Ç–æ—á–∫–µ –≤—Ö–æ–¥–∞.

const cart = () => {
  // 2.3.4 (–Ω–∞—á–∞–ª–æ –≤ render-goods.js)
  // * 4.1 –ó–∞–π–º—ë–º—Å—è –≤—ã—á–∏—Å–ª–µ–Ω–∏–µ–º –æ–±—â–µ–π —Å—É–º–º—ã –≤—Å–µ—Ö —Ç–æ–≤–∞—Ä–æ–≤. –î–ª—è —ç—Ç–æ–≥–æ —Å–ø–µ—Ä–≤–∞ –ø–æ–ª—É—á–∏–º —ç–ª–µ–º–µ–Ω—Ç, –≤ –∫–æ—Ç–æ—Ä—ã–π –º—ã —Å—Ç–∞–Ω–µ–º –æ—Ç—Ä–∏—Å–æ–≤—ã–≤–∞—Ç—å —ç—Ç—É —Å—É–º–º—É –∏ –∑–∞–ø–∏—à–µ–º –µ–≥–æ –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é cartTotalSum.
  // * 5.1 –ù–∞–º —Ç–∞–∫–∂–µ –Ω—É–∂–Ω–æ –º–æ—á—å —É–¥–∞–ª—è—Ç—å —Ç–æ–≤–∞—Ä—ã –∏–∑ –Ω–∞—à–µ–π –∫–æ—Ä–∑–∏–Ω—ã. –î–ª—è —ç—Ç–æ–≥–æ –ø–æ–ª—É—á–∏–º –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä, –≤ –∫–æ—Ç–æ—Ä–æ–º –æ–Ω–∏ –Ω–∞—Ö–æ–¥—è—Ç—Å—è –≤ –∫–æ—Ä–∑–∏–Ω–µ —Ç–æ–≤–∞—Ä–æ–≤.
  // * 6.1 –û—Å—Ç–∞–ª–æ—Å—å —Ä–µ–∞–ª–∏–∑–æ–≤–∞—Ç—å –æ—Ç–ø—Ä–∞–≤–∫—É –≤—Å–µ–≥–æ –º–∞—Å—Å–∏–≤–∞ "cart". –î–ª—è —ç—Ç–æ–≥–æ –ø–æ–ª—É—á–∏–º –∫–Ω–æ–ø–∫—É –≤–Ω—É—Ç—Ä–∏ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ –∫–æ—Ä–∑–∏–Ω—ã "–û—Ñ–æ—Ä–º–∏—Ç—å –∑–∞–∫–∞–∑".
  const cartButton = document.querySelector('#cart'),
    cartModal = document.querySelector('.cart'),
    cartCloseButton = cartModal.querySelector('.cart-close'),
    cartTotalSum = cartModal.querySelector('.cart-total > span'),
    cartWrapper = cartModal.querySelector('.cart-wrapper'),
    cartSendButton = cartModal.querySelector('.cart-confirm'),
    goodsWrapper = document.querySelector('.goods');

  // 3.2 –ü—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ –∫–Ω–æ–ø–∫—É –∫–æ—Ä–∑–∏–Ω—ã —É –Ω–∞—Å –æ—Ç—Ä–∞–±–æ—Ç–∞–µ—Ç —Ñ—É–Ω–∫—Ü–∏—è openCart() –∏ –Ω–∞–º –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –ø–æ–ª—É—á–∏—Ç—å –≤ —ç—Ç–æ–π —Ñ—É–Ω–∫—Ü–∏–∏ –≤—Å—é –Ω–∞—à—É –∫–æ—Ä–∑–∏–Ω—É —Å –¥–æ–±–∞–≤–ª–µ–Ω–Ω—ã–º–∏ —Ç–æ–≤–∞—Ä–∞–º–∏. –î–ª—è —ç—Ç–æ–≥–æ –ø–æ–ª—É—á–∏–º –Ω–∞—à—É –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é cart —Å –¥–∞–Ω–Ω—ã–º–∏ –∏–∑ localStorage –≤ –Ω–µ–π, –∞ –∑–∞—Ç–µ–º –∑–∞–ø—É—Å—Ç–∏–º —Ñ—É–Ω–∫—Ü–∏—é renderCart() –¥–ª—è –æ—Ç—Ä–∏—Å–æ–≤–∫–∏ —ç—Ç–∏—Ö —Ç–æ–≤–∞—Ä–æ–≤ –≤ –∫–æ—Ä–∑–∏–Ω–µ –∏ –ø–µ—Ä–µ–¥–∞–¥–∏–º —Ç—É–¥–∞ –¥–∞–Ω–Ω—ã–µ –∏–∑ cart.

  const openCart = () => {
    const cart = localStorage.getItem('cart')
      ? JSON.parse(localStorage.getItem('cart'))
      : [];

    cartModal.style.display = 'flex';

    renderCart(cart);
    // 4.2 –ü—Ä–∏ –æ—Ç–∫—Ä—ã—Ç–∏–∏ –∫–æ—Ä–∑–∏–Ω—ã —Ç–∞–∫–∂–µ –∏ —Ç–µ–∫—Å—Ç–æ–≤–æ–µ —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ –æ–±—â–µ–π —Å—É–º–º—ã —Ü–µ–Ω—ã —Ç–æ–≤–∞—Ä–æ–≤ –º—É —Ä–∞—Å—Å—á–∏—Ç–∞–µ–º –∏ –≤–∏–¥–æ–∏–∑–º–µ–Ω–∏–º. –î–ª—è —ç—Ç–æ–≥–æ –æ–±—Ä–∞—Ç–∏–º—Å—è –∫ –º–∞—Å—Å–∏–≤—É cart –∏ –º–µ—Ç–æ–¥–æ–º reduce() –ø–µ—Ä–µ–±–µ—Ä—ë–º –º–∞—Å—Å–∏–≤. –û–Ω –ø—Ä–∏–º–µ—Ç callback-—Ñ—É–Ω–∫—Ü–∏—é, –∞ –≤—Ç–æ—Ä—ã–º –∞—Ä–≥—É–º–µ–Ω—Ç–æ–º –Ω–∞—á–∞–ª—å–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ ("0") –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞, –≤ –∫–æ—Ç–æ—Ä–æ–º –º—ã –±—É–¥–µ–º —Ä–∞—Å—Å—á–∏—Ç—ã–≤–∞—Ç—å –∫–æ–Ω–µ—á–Ω—É—é —Å—É–º–º—É –≤—Å–µ—Ö —Ç–æ–≤–∞—Ä–æ–≤, –ø–æ–ª—É—á–µ–Ω–Ω—É—é –ø—Ä–∏ –ø–µ—Ä–µ–±–æ—Ä–µ —ç–ª–µ–º–µ–Ω—Ç–æ–≤.
    // 4.3 –û–Ω —Ç–∞–∫–∂–µ –ø—Ä–∏–º–µ—Ç —Ç–∞–∫–∏–µ –∞—Ä–≥—É–º–µ–Ω—Ç—ã, –∫–∞–∫ sum - –Ω–µ–∫–∏–π –∞–∫–∫—É–º—É–ª—è—Ç–æ—Ä, –∫–æ—Ç–æ—Ä—ã–π –±—É–¥–µ—Ç –Ω–∞–∫–∞–ø–ª–∏–≤–∞—Ç—å —Å—É–º–º—É —Ü–µ–Ω —Ç–æ–≤–∞—Ä–æ–≤, –∞ —Ç–∞–∫–∂–µ goodItem - –∫–∞–∂–¥—ã–π –∏—Ç–µ—Ä–∏—Ä—É–µ–º—ã–π —ç–ª–µ–º–µ–Ω—Ç.
    cartTotalSum.textContent = cart.reduce((sum, goodItem) => {
      return sum + goodItem.price; // —Ç–∞–∫ –ª–µ–≥–∫–æ —Å—á–∏—Ç–∞–µ—Ç—Å—è –∏—Ç–æ–≥–æ–≤–∞—è —Å—É–º–º–∞ –≤ –º–∞—Å—Å–∏–≤–µ
    }, 0);
  };

  const closeCart = () => {
    cartModal.style.display = '';
  };

  cartButton.addEventListener('click', openCart);
  cartCloseButton.addEventListener('click', closeCart);
  // 2.4.1 –¢–µ–ø–µ—Ä—å –Ω–∞–º –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –æ—Ç—Å–µ—á—å –≤—Å–µ —ç–ª–µ–º–µ–Ω—Ç—ã –∫—Ä–æ–º–µ —Å–∞–º–æ–π –∫–Ω–æ–ø–∫–∏ ¬´–í –∫–æ—Ä–∑–∏–Ω—É¬ª. –î–ª—è —ç—Ç–æ–≥–æ –º—ã –∏—Å–ø–æ–ª—å–∑—É–µ–º event.target, –¥–ª—è –æ–±–Ω–∞—Ä—É–∂–µ–Ω–∏—è –∫–Ω–æ–ø–∫–∏. –ò–∑ –≤—ë—Ä—Å—Ç–∫–∏ –º—ã —É–≤–∏–¥–µ–ª–∏ —É –Ω–µ—ë –∫–ª–∞—Å—Å ".btn-primary", –≤–æ—Ç –ø–æ –Ω–µ–º—É –º—ã –∏ –±—É–¥–µ–º –ø—Ä–æ–≤–µ—Ä—è—Ç—å –Ω—É–∂–Ω—É—é –Ω–∞–º –∫–Ω–æ–ø–∫—É.
  // 2.4.2 –ü–æ –∫–ª–∏–∫—É –Ω–∞ –∫–Ω–æ–ø–∫—É –Ω–∞–º –Ω—É–∂–Ω–æ –±—É–¥–µ—Ç –Ω–∞–π—Ç–∏ —Ä–æ–¥–∏—Ç–µ–ª—å—Å–∫–∏–π —Ç–µ–≥ —Å –∫–ª–∞—Å—Å–æ–º ".card" –¥–ª—è —Ç–æ–≥–æ, —á—Ç–æ–±—ã —Å –Ω–µ–≥–æ —Å—á–∏—Ç–∞—Ç—å –Ω–∞—à –¥–∞—Ç–∞-–∞—Ç—Ä–∏–±—É—Ç —Å id —Ç–æ–≤–∞—Ä–∞.
  // 2.4.3 –î–∞–ª–µ–µ —Å –ø–æ–º–æ—â—å—é –º–µ—Ç–æ–¥–∞ dataset.id –º—ã —Å—á–∏—Ç–∞–µ–º –∑–Ω–∞—á–µ–Ω–∏–µ –Ω–∞—à–µ–≥–æ –¥–∞—Ç–∞-–∞—Ç—Ä–∏–±—É—Ç–∞.
  // 2.4.4 –û—Å—Ç–∞–ª–æ—Å—å –¥–æ—Å—Ç–∞—Ç—å –∏–∑ localStorage –Ω—É–∂–Ω—ã–π –æ–±—ä–µ–∫—Ç —Å —ç—Ç–∏–º id –∏ –∑–∞–ø–∏—Å–∞—Ç—å –µ–≥–æ –≤ –º–∞—Å—Å–∏–≤. –ê –¥–∞–ª–µ–µ —Å–æ—Ö—Ä–∞–Ω–∏—Ç—å —ç—Ç–æ—Ç –º–∞—Å—Å–∏–≤ –≤ localStorage. –ü–æ–ª—É—á–∏–º –≤–µ—Å—å –º–∞—Å—Å–∏–≤ –∏–∑ localStorage –º–µ—Ç–æ–¥–æ–º getItem() —Å –∫–ª—é—á–æ–º "goods". –¢–∞–∫ –º—ã –ø–æ–ª—É—á–∏–º –¥–∞–Ω–Ω—ã–µ –≤ –≤–∏–¥–µ —Å—Ç—Ä–æ—á–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö. –ù–æ –¥–ª—è —Ç–æ–≥–æ, —á—Ç–æ–±—ã –Ω–∞–º –ø–æ–ª—É—á–∏—Ç—å –º–∞—Å—Å–∏–≤ –º—ã –º–æ–∂–µ–º –∫–æ–Ω–≤–µ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å –∏—Ö –≤ –º–∞—Å—Å–∏–≤ –º–µ—Ç–æ–¥–æ–º JSON.parse()
  // 2.4.5 –¢–µ–ø–µ—Ä—å –∏–∑ —ç—Ç–æ–≥–æ –º–∞—Å—Å–∏–≤–∞ –Ω–∞–º –Ω—É–∂–Ω–æ –¥–æ—Å—Ç–∞—Ç—å –æ–±—ä–µ–∫—Ç —Å –Ω—É–∂–Ω—ã–º –Ω–∞–º id –∏ –∑–∞–ø–∏—Å–∞—Ç—å –µ–≥–æ –≤ –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤ –Ω–∞—à–µ–π –∫–æ—Ä–∑–∏–Ω–∫–∏. –î–ª—è —ç—Ç–æ–≥–æ —Å–æ–∑–¥–∞–¥–∏–º –µ—â—ë –æ–¥–Ω—É –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é "cart" –≤ –∫–æ—Ç–æ—Ä–æ–π –ø–æ–∫–∞ –±—É–¥–µ—Ç –ø—É—Å—Ç–æ–π –º–∞—Å—Å–∏–≤.
  // 2.4.6 –ù–æ –µ—Å–ª–∏ —É –Ω–∞—Å —Ç–µ–ø–µ—Ä—å —É–∂–µ –±—É–¥–µ—Ç "cart" –≤ localStorage, —Ç–æ –Ω–∞–º —Ç–∞–∫–∂–µ –Ω—É–∂–Ω–æ –ø—Ä–æ–≤–µ—Ä—è—Ç—å –µ—Å—Ç—å –ª–∏ —Ç–∞–∫–æ–π –∫–ª—é—á –≤ localStorage —Å –ø–æ–º–æ—â—å—é —Ç–µ—Ä–Ω–∞—Ä–Ω–æ–≥–æ –æ–ø–µ—Ä–∞—Ç–æ—Ä–∞, –∫–æ—Ç–æ—Ä—ã–π –æ–∑–Ω–∞—á–∞–µ—Ç —Å–ª–µ–¥—É—é—â–µ–µ: –ï—Å–ª–∏ —É –Ω–∞—Å –≤ localStorage –µ—Å—Ç—å –º–∞—Å—Å–∏–≤ —Å –∫–ª—é—á–æ–º "cart", —Ç–æ–≥–¥–∞ –º—ã –∑–∞–Ω–µ—Å—ë–º –≤ –∑–Ω–∞—á–µ–Ω–∏–µ –∫–ª—é—á–∞ "cart" –º–∞—Å—Å–∏–≤ —Å –∫–ª—é—á–æ–º "cart", –∏–Ω–∞—á–µ –≤–µ—Ä–Ω—ë–º –ø—É—Å—Ç–æ–π –º–∞—Å—Å–∏–≤. –¢–µ—Ä–Ω–∞—Ä–Ω—ã–π –æ–ø–µ—Ä–∞—Ç–æ—Ä —Å–æ—Å—Ç–æ–∏—Ç –∏–∑ "—É—Å–ª–æ–≤–∏–µ" ? "–ø–µ—Ä–≤—ã–π —Ä–µ–∑—É–ª—å—Ç–∞—Ç, –µ—Å–ª–∏ –≤–µ—Ä–Ω—ë—Ç—Å—è true" : "–≤—Ç–æ—Ä–æ–π —Ä–µ–∑—É–ª—å—Ç–∞—Ç, –µ—Å–ª–∏ –≤–µ—Ä–Ω—ë—Ç—Å—è false".
  // 2.4.7 –¢–µ–ø–µ—Ä—å –Ω–∞—à–µ–π –∑–∞–¥–∞—á–µ–π –±—É–¥–µ—Ç –¥–æ—Å—Ç–∞—Ç—å –∏–∑ –º–∞—Å—Å–∏–≤–∞ "goods" –Ω–µ–æ–±—Ö–æ–¥–∏–º—ã–π –Ω–∞–º –æ–±—ä–µ–∫—Ç (–∫–æ—Ç–æ—Ä—ã–π –¥–æ–±–∞–≤–ª—è–µ–º –≤ –∫–æ—Ä–∑–∏–Ω–∫—É), –∑–∞–ø–∏—Å–∞—Ç—å –µ–≥–æ –≤ –º–∞—Å—Å–∏–≤ "cart" –∏ —Å–æ—Ö—Ä–∞–Ω–∏—Ç—å –º–∞—Å—Å–∏–≤ "cart" –≤ localStorage —Å –∫–ª—é—á–æ–º "cart". –î–ª—è —ç—Ç–æ–≥–æ —Å–æ–∑–¥–∞–¥–∏–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é goodItem, –∫–æ—Ç–æ—Ä–æ–π –ø—Ä–∏—Å–≤–æ–∏–º –º–∞—Å—Å–∏–≤ goods —Å –º–µ—Ç–æ–¥–æ–º –ø–µ—Ä–µ–±–æ—Ä–∞ –º–∞—Å—Å–∏–≤–∞ find(), –∫–æ—Ç–æ—Ä—ã–π –±—É–¥–µ—Ç –∏—Å–∫–∞—Ç—å —Å–æ–≤–ø–∞–¥–µ–Ω–∏—è —Å –Ω–∞—à–∏–º –º–∞—Å—Å–∏–≤–æ–º –∏ –≤–æ–∑–≤—Ä–∞—â–∞—Ç—å –ø–µ—Ä–≤—ã–π –æ–±—ä–µ–∫—Ç –∏–∑ –º–∞—Å—Å–∏–≤–∞, –∫–æ—Ç–æ—Ä—ã–π –ø–æ–¥–æ–π–¥—ë—Ç –Ω–∞–º –ø–æ–¥ —É—Å–ª–æ–≤–∏–µ. –£ –Ω–µ–≥–æ —Ç–∞–∫–∂–µ –±—É–¥–µ—Ç callback-—Ñ—É–Ω–∫—Ü–∏—è, –∫–æ—Ç–æ—Ä–∞—è –≤ —Å–≤–æ—é –æ—á–µ—Ä–µ–¥—å –ø–æ–ª—É—á–∞–µ—Ç –∫–∞–∂–¥—ã–π –∏—Ç–µ—Ä–∏—Ä—É–µ–º—ã–π item. –ê –≤–æ–∑–≤—Ä–∞—â–∞—Ç—å –º—ã –±—É–¥–µ–º –∑–Ω–∞—á–µ–Ω–∏–µ id —ç—Ç–æ–≥–æ item‚Äô–∞ —Ä–∞–≤–Ω–æ–≥–æ –ø–æ–ª—É—á–µ–Ω–Ω–æ–º—É id –∏–∑ –¥–∞—Ç–∞-–∞—Ç—Ä–∏–±—É—Ç–∞ –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω–æ–π "id". –ü—Ä–∞–≤–¥–∞ –æ–Ω –≤ –≤–∏–¥–µ —Å—Ç—Ä–æ–∫–∏, –ø–æ—ç—Ç–æ–º—É –Ω–∞–ø–∏—à–µ–º —É–Ω–∞—Ä–Ω—ã–π –ø–ª—é—Å –ø–µ—Ä–µ–¥ –Ω–∏–º, —á—Ç–æ–±—ã –ø—Ä–µ–≤—Ä–∞—Ç–∏—Ç—å –≤ —á–∏—Å–ª–æ–≤–æ–π –≤–∏–¥ –¥–∞–Ω–Ω—ã—Ö.
  // 2.4.8 –î–æ–±–∞–≤–∏–º –ø–æ–ª—É—á–µ–Ω–Ω—ã–π goodItem –≤ –º–∞—Å—Å–∏–≤ cart –∏ –ø–µ—Ä–µ–∑–∞–ø–∏—à–µ–º –º–∞—Å—Å–∏–≤ cart –≤ localStorage. –î–ª—è —ç—Ç–æ–≥–æ –º—ã –æ–±—Ä–∞—Ç–∏–º—Å—è –∫ cart –º–µ—Ç–æ–¥–æ–º push() –∏ –ø–µ—Ä–µ–¥–∞–¥–∏–º –µ–º—É goodItem.
  // 2.4.9 –î–∞–ª–µ–µ –≤—Å—ë –∫–∞–∫ –º—ã –¥–µ–ª–∞–ª–∏ —Å –º–∞—Å—Å–∏–≤–æ–º goods - localStorage.setItem('cart', JSON.stringify(cart))
  // 3.1 –¢–µ–ø–µ—Ä—å, –∫–æ–≥–¥–∞ —É –Ω–∞—Å –¥–æ–±–∞–≤–ª—è—é—Ç—Å—è —Ç–æ–≤–∞—Ä—ã –≤ —Å–ø–µ—Ü–∏–∞–ª—å–Ω—ã–π –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä "cart" –≤ localStorage –º—ã –º–æ–∂–µ–º –æ—Ç—Ä–∏—Å–æ–≤–∞—Ç—å –∏—Ö –≤ –º–æ–¥–∞–ª—å–Ω–æ–º –æ–∫–Ω–µ –∫–æ—Ä–∑–∏–Ω—ã –ø–æ –∫–ª–∏–∫—É –Ω–∞ –∫–Ω–æ–ø–∫—É –∫–æ—Ä–∑–∏–Ω—ã. (—Å–º. render-cart.js)
  goodsWrapper.addEventListener('click', (evt) => {
    if (evt.target.classList.contains('btn-primary')) {
      const card = evt.target.closest('.card');
      const id = card.dataset.id;
      const goods = JSON.parse(localStorage.getItem('goods'));
      const cart = localStorage.getItem('cart')
        ? JSON.parse(localStorage.getItem('cart'))
        : [];
      const goodItem = goods.find((item) => {
        return item.id === +id;
      });

      cart.push(goodItem);

      localStorage.setItem('cart', JSON.stringify(cart));
    }
  });
  // 5.2 –¢–∞–∫–∂–µ –∏ –∑–¥–µ—Å—å –º—ã –≤–æ—Å–ø–æ–ª—å–∑—É–µ–º—Å—è –¥–µ–ª–µ–≥–∏—Ä–æ–≤–∞–Ω–∏–µ–º –∏ –ø–æ–≤–µ—Å–∏–º –Ω–∞ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä —Å —Ç–æ–≤–∞—Ä–∞–º–∏ –≤ –∫–æ—Ä–∑–∏–Ω–µ cartWrapper –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è –ø–æ –∫–ª–∏–∫—É –∏ —Å–¥–µ–ª–∞–µ–º —Ç–∞–∫–æ–µ –∂–µ —É—Å–ª–æ–≤–∏–µ, —á—Ç–æ —Ç–æ–ª—å–∫–æ –ø—Ä–∏ —É—Å–ª–æ–≤–∏–∏ –Ω–∞—Ö–æ–∂–¥–µ–Ω–∏—è —É —ç–ª–µ–º–µ–Ω—Ç–∞ –∫–ª–∞—Å—Å–∞ "btn-primary" –º—ã —Å–ø–µ—Ä–≤–∞ –±—É–¥–µ–º –Ω–∞—Ö–æ–¥–∏—Ç—å –≤–µ—Å—å –º–∞—Å—Å–∏–≤ cart.
  cartWrapper.addEventListener('click', (evt) => {
    if (evt.target.classList.contains('btn-primary')) {
      const cart = localStorage.getItem('cart')
        ? JSON.parse(localStorage.getItem('cart'))
        : [];
      // 5.3 –î–∞–ª–µ–µ –º—ã –ø–æ–ª—É—á–∞–µ–º —Ç–æ—Ç —ç–ª–µ–º–µ–Ω—Ç, –∫–æ—Ç–æ—Ä—ã–π –º—ã –∫–ª–∏–∫–Ω—É–ª–∏.
      const card = evt.target.closest('.card');
      const id = card.dataset.id;
      // 5.7 –ò—Ç–∞–∫, —á—Ç–æ–±—ã —É–¥–∞–ª–∏—Ç—å –∫–∞–∫–æ–π-—Ç–æ —ç–ª–µ–º–µ–Ω—Ç –∏–∑ –º–∞—Å—Å–∏–≤—ã "cart" –Ω–∞–º –Ω—É–∂–Ω–æ —É–∑–Ω–∞—Ç—å –∏–Ω–¥–µ–∫—Å —ç—Ç–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞ –≤–Ω—É—Ç—Ä–∏ –º–∞—Å—Å–∏–≤–∞. –î–ª—è —ç—Ç–æ–≥–æ —Å–æ–∑–¥–∞–¥–∏–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é index. –í –Ω–µ—ë –ø–æ–º–µ—Å—Ç–∏–º –∑–Ω–∞—á–µ–Ω–∏–µ, –ø–æ–ª—É—á–µ–Ω–Ω–æ–µ –∏–∑ cart —Å –ø–æ–º–æ—â—å—é –º–µ—Ç–æ–¥–∞ findIndex(), –∫–æ—Ç–æ—Ä—ã–π –ø—Ä–∏–º–µ—Ç —Ñ—É–Ω–∫—Ü–∏—é-callback, –∫–æ—Ç–æ—Ä–∞—è –≤–µ—Ä–Ω—ë—Ç –∏–Ω–¥–µ–∫—Å –Ω–∞–π–¥–µ–Ω–Ω–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞. –¢–∞–∫–∂–µ –º—ã —Å—Ä–∞–≤–Ω–∏–º item.id c–æ –∑–Ω–∞—á–µ–Ω–∏–µ–º –ø–µ—Ä–µ–º–µ–Ω–Ω–æ–π id.
      const index = cart.findIndex((item) => {
        return item.id === +id;
      });
      // 5.8 –¢–µ–ø–µ—Ä—å –º—ã –ø—Ä–∏–º–µ–Ω–∏–º –∫ –º–∞—Å—Å–∏–≤—É cart –º–µ—Ç–æ–¥ splice(), –∫–æ—Ç–æ—Ä—ã–π –ø—Ä–∏–º–µ—Ç –¥–≤–∞ –∞—Ä–≥—É–º–µ–Ω—Ç–∞: 1) –∏–Ω–¥–µ–∫—Å —É–¥–∞–ª—è–µ–º–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞, 2) –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —ç–ª–µ–º–µ–Ω—Ç–æ–≤.
      cart.splice(index, 1);
      // 5.4 –ù—É –∏ –≤ –∏—Ç–æ–≥–µ —É–¥–∞–ª–∏—Ç—å –∏–∑ –º–∞—Å—Å–∏–≤–∞ "cart" —ç–ª–µ–º–µ–Ω—Ç —Å —Ç–µ–º id, –ø–æ –∫–æ—Ç–æ—Ä–æ–º—É –º—ã –∫–ª–∏–∫–Ω—É–ª–∏ –∏ —Å–Ω–æ–≤–∞ –∑–∞–ø–∏—Å–∞—Ç—å –≤ localStorage –æ–±–Ω–æ–≤–ª—ë–Ω–Ω—ã–π –º–∞—Å—Å–∏–≤ –¥–∞–Ω–Ω—ã—Ö.
      localStorage.setItem('cart', JSON.stringify(cart));
      // 5.5 –ò —Å–Ω–æ–≤–∞ –≤—ã–∑–æ–≤–µ–º —Ñ—É–Ω–∫—Ü–∏—é –æ—Ç—Ä–∏—Å–æ–≤–∫–∏ —Ç–æ–≤–∞—Ä–∞ renderCart()
      renderCart(cart);
      // 5.6 –ê —Ç–∞–∫–∂–µ –ø–æ–¥—Å—á—ë—Ç –∏—Ç–æ–≥–æ–≤–æ–π —Å—Ç–æ–∏–º–æ—Å—Ç–∏
      cartTotalSum.textContent = cart.reduce((sum, goodItem) => {
        return sum + goodItem.price;
      }, 0);
    }
  });
  // 6.2 –ü–æ–≤–µ—Å–∏–º –Ω–∞ –∫–Ω–æ–ø–∫—É –æ—Ç–ø—Ä–∞–≤–∫–∏ —Ç–æ–≤–∞—Ä–æ–≤ –∏–∑ –∫–æ—Ä–∑–∏–Ω—ã —Ç–∞–∫–∂–µ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è –ø–æ –∫–ª–∏–∫—É
  cartSendButton.addEventListener('click', () => {
    // 6.3 –ü–æ—Å–ª–µ –∫–ª–∏–∫–∞ –ø–æ –∫–Ω–æ–ø–∫–µ –Ω–∞–º –Ω—É–∂–Ω–æ –ø–æ–ª—É—á–∏—Ç—å –º–∞—Å—Å–∏–≤ "cart" –∏–ª–∏ –ø—É—Å—Ç–æ–π –º–∞—Å—Å–∏–≤.
    const cart = localStorage.getItem('cart')
      ? JSON.parse(localStorage.getItem('cart'))
      : [];
    // 6.4 –í—ã–∑–æ–≤–µ–º —Ñ—É–Ω–∫—Ü–∏—é –ø–µ—Ä–µ–¥–∞—á–∏ –¥–∞–Ω–Ω—ã—Ö –Ω–∞ —Å–µ—Ä–≤–µ—Ä postData() –∏ –ø–µ—Ä–µ–¥–∞–¥–∏–º –≤ –Ω–µ—ë –º–∞—Å—Å–∏–≤ cart.
    // 6.5 (—Å–º. post-data.js)
    // * 7.1 –¢–µ–ø–µ—Ä—å –ø–æ—Å–ª–µ –æ—Ç–ø—Ä–∞–≤–∫–∏ –Ω–∞—à–µ–π –∫–æ—Ä–∑–∏–Ω—ã –Ω–∞ —Å–µ—Ä–≤–µ—Ä –Ω–∞–º –Ω—É–∂–Ω–æ –µ—ë –æ—á–∏—Å—Ç–∏—Ç—å –∏ –ø–µ—Ä–µ—Ä–∏—Å–æ–≤–∞—Ç—å. –ó–¥–µ—Å—å –ø–æ–¥–æ–π–¥—ë—Ç –º–µ—Ç–æ–¥ then(), –∫–æ—Ç–æ—Ä—ã–π –≤—ã–∑–æ–≤–µ—Ç—Å—è —Å—Ä–∞–∑—É, –ø–æ—Å–ª–µ —Ç–æ–≥–æ –∫–∞–∫ –ø—Ä–æ–∏–∑–æ–π–¥—ë—Ç —É—Å–ø–µ—à–Ω–∞—è –æ—Ç–ø—Ä–∞–≤–∫–∞ –¥–∞–Ω–Ω—ã—Ö.
    // 7.2 –°–Ω–∞—á–∞–ª–∞ –ø—Ä–∏–º–µ–Ω–∏–º –Ω–∞ localStorage –º–µ—Ç–æ–¥ removeItem() —Å –∫–ª—é—á–æ–º "cart", —á—Ç–æ–±—ã –æ—á–∏—Å—Ç–∏—Ç—å –ø–∞–º—è—Ç—å –æ—Ç —ç—Ç–æ–≥–æ –º–∞—Å—Å–∏–≤–∞.
    postData(cart).then(() => {
      localStorage.removeItem('cart');
      // 7.3 –ü–µ—Ä–µ—Ä–∏—Å—É–µ–º –ø–µ—Ä–µ–¥–∞–≤ –ø—É—Å—Ç–æ–π –º–∞—Å—Å–∏–≤...
      renderCart([]);
      // 7.4 ...–∏ –ø–µ—Ä–µ—Å—á–∏—Ç–∞–µ–º –æ–±—â—É—é —Ü–µ–Ω—É, —Ç–æ—á–Ω–µ–µ –ø–æ–¥—Å—Ç–∞–≤–∏–º 0, –≤–µ–¥—å –∫–æ—Ä–∑–∏–Ω–∞ —Ç–µ–ø–µ—Ä—å –ø—É—Å—Ç–∞.
      cartTotalSum.textContent = 0;
    });
  });
};

export default cart;
