import getData from './get-data.js';
import renderGoods from './render-goods.js';
import { funcFilter } from './filters.js';
import { debounce } from './helpers.js';

const search = () => {
  const searchInput = document.querySelector('.search-wrapper_input');
  // 2.0 –ü—Ä–æ–≥—Ä–∞–º–º–∏—Ä—É–µ–º —Ñ–∏–ª—å—Ç—Ä —Ü–µ–Ω
  // 2.1 –°–Ω–∞—á–∞–ª–∞ –ø–æ–ª—É—á–∏–º –∏–∑ –≤—ë—Ä—Å—Ç–∫–∏ —Å—Ç—Ä–æ–∫–∏ –≤–≤–æ–¥–∞ –º–∏–Ω–∏–º–∞–ª—å–Ω–æ–π —Ü–µ–Ω—ã —Ñ–∏–ª—å—Ç—Ä–∞ –∏ –º–∞–∫—Å–∏–º–∞–ª—å–Ω–æ–π
  const minInput = document.querySelector('#min');
  const maxInput = document.querySelector('#max');
  // 3.1 –î–æ–±–∞–≤–∏–º —Ç–∞–∫–∂–µ —Ñ—É–Ω–∫—Ü–∏—é —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏ —Ç–æ–≤–∞—Ä–æ–≤ –ø–æ –∞–∫—Ü–∏–∏, –¥–ª—è —ç—Ç–æ–≥–æ –ø–æ–ª—É—á–∏–º –∏–Ω–ø—É—Ç —á–µ–∫–±–æ–∫—Å–∞
  const checkbox = document.querySelector('#discount-checkbox');
  // 3.3 –¢–∞–∫–∂–µ –ø–æ–ª—É—á–∏–º —ç–ª–µ–º–µ–Ω—Ç —á–µ–∫–±–æ–∫—Å–∞, –∫–æ—Ç–æ—Ä—ã–π —Å–≤—è–∑–∞–Ω —Å –∏–Ω–ø—É—Ç–æ–º (–∫–∞—Å—Ç–æ–º–Ω—ã–π —á–µ–∫–±–æ–∫—Å), —á—Ç–æ–±—ã –Ω–∞ –Ω–µ–≥–æ –≤–µ—à–∞—Ç—å –∫–ª–∞—Å—Å "checked" –¥–ª—è –ø–æ—è–≤–ª–µ–Ω–∏—è –≥–∞–ª–æ—á–∫–∏, –ø—Ä–∏ –≤—ã–±–æ—Ä–µ.
  const checkMark = document.querySelector('.filter-check_checkmark');

  // 2.3 –¢–∞–∫–∂–µ —Å–æ–∑–¥–∞–¥–∏–º –ø–æ—Ö–æ–∂—É—é debounce —Ñ—É–Ω–∫—Ü–∏—é, –∫–∞–∫ –∏ —Å –æ–±—â–∏–º –ø–æ–∏—Å–∫–æ–º, –Ω–æ —É–∂–µ –ø–æ —Ñ–∏–ª—å—Ç—Ä–∞—Ü–∏–∏ —Ü–µ–Ω. –£–∫–∞–∂–µ–º –∞—Ä–≥—É–º–µ–Ω—Ç–∞–º–∏ –º–∏–Ω–∏–º–∞–ª—å–Ω—É—é –∏ –º–∞–∫—Å–∏–º–∞–ª—å–Ω—É—é —Ü–µ–Ω—ã.
  // 2.4 –û–¥–Ω–∞–∫–æ searchFilter –Ω—É–∂–Ω–æ –±—É–¥–µ—Ç –Ω–µ–º–Ω–æ–≥–æ –ø–µ—Ä–µ–¥–µ–ª–∞—Ç—å. –ü–µ—Ä–µ–π–¥—ë–º –¥–ª—è —ç—Ç–æ–≥–æ –≤ filters.js.
  // 2.5 –í —Å–∞–º—É —Ñ—É–Ω–∫—Ü–∏—é —Ñ–∏–ª—å—Ç—Ä–∞ —Ü–µ–Ω –±—É–¥–µ—Ç –ø–µ—Ä–µ–¥–∞–≤–∞—Ç—å—Å—è –¥–∞—Ç–∞ (–≤–µ—Å—å —Ç–æ–≤–∞—Ä), –º–∏–Ω–∏–º–∞–ª—å–Ω–∞—è –∏ –º–∞–∫—Å–∏–º–∞–ª—å–Ω–∞—è —Ü–µ–Ω–∞.
  // 3.4.1 –¢–∞–∫–∂–µ –¥–æ–±–∞–≤–∏–º –∏ –∞—Ç—Ä–∏–±—É—Ç –¥–ª—è —á–µ–∫–±–æ–∫—Å–∞ ¬´–ê–∫—Ü–∏–∏¬ª check —Å –¥–µ—Ñ–æ–ª—Ç–Ω—ã–º –∑–Ω–∞—á–µ–Ω–∏–µ ¬´false¬ª.
  const debounceFunc = debounce(
    (min = '', max = '', checkValue = false, searchValue = '') => {
      getData().then((data) => {
        renderGoods(funcFilter(data, min, max, checkValue, searchValue));
      });
    },
    700
  );
  // 4.2 (–ù–∞—á–∞–ª–æ –≤ filters.js) –î–æ–±–∞–≤–∏–º –∫ –∑–Ω–∞–∫–æ–º–æ–º—É debounceFunc() –≤ –∞—Ç—Ä–∏–±—É—Ç—ã –µ—â—ë searchInput.value, –∫–∞–∫ –∏ –≤ –¥—Ä—É–≥–∏–µ —Ñ—É–Ω–∫—Ü–∏–∏ —Ç–æ–∂–µ.
  searchInput.addEventListener('input', () =>
    debounceFunc(
      minInput.value,
      maxInput.value,
      checkbox.value,
      searchInput.value
    )
  );

  // 2.2 –ü–æ–≤–µ—Å–∏–º –Ω–∞ –∫–∞–∂–¥—ã–π –∏–∑ –Ω–∏—Ö –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∏ —Å–æ–±—ã—Ç–∏—è.
  // 3.4.2 –¢–∞–∫–∂–µ –∏ —Å—é–¥–∞
  minInput.addEventListener('input', () =>
    debounceFunc(
      minInput.value,
      maxInput.value,
      checkbox.value,
      searchInput.value
    )
  );
  maxInput.addEventListener('input', () =>
    debounceFunc(
      minInput.value,
      maxInput.value,
      checkbox.value,
      searchInput.value
    )
  );
  // 3.2 –ù–∞ —á–µ–∫–±–æ–∫—Å –ø–æ–≤–µ—Å–∏–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è, –∫–æ—Ç–æ—Ä—ã–π –ø–æ –∫–ª–∏–∫—É –±—É–¥–µ—Ç –≤–µ—à–∞—Ç—å –∫–ª–∞—Å—Å, –∫–æ—Ç–æ—Ä—ã–π –æ—Ç–æ–±—Ä–∞–∑–∏—Ç –≥–∞–ª–æ—á–∫—É –≤ —á–µ–∫–±–æ–∫—Å–µ.
  checkbox.addEventListener('input', () => {
    if (checkbox.checked) {
      checkMark.classList.add('checked');
    } else {
      checkMark.classList.remove('checked');
    }
    // 3.4.3 –ò —Å—é–¥–∞
    // 3.5.1 –¢–∞–∫–∂–µ –∏ –¥–æ–±–∞–≤–∏–º checkValue –≤ filters.js
    debounceFunc(
      minInput.value,
      maxInput.value,
      checkbox.checked,
      searchInput.value
    );
  });
};

export default search;
