// === –°–ø–µ—Ü–∏–∞–ª—å–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è-–ø–æ–º–æ—â–Ω–∏–∫ ¬´Debounce¬ª, –∫–æ—Ç–æ—Ä–∞—è –ø–æ–∑–≤–æ–ª—è–µ—Ç —Å–æ–∑–¥–∞–≤–∞—Ç—å –æ—Ç–ª–æ–∂–µ–Ω–Ω—ã–µ –∑–∞–ø—Ä–æ—Å—ã –Ω–∞ —Å–µ—Ä–≤–µ—Ä, –ø—Ä–µ–¥–æ—Ç–≤—Ä–∞—â–∞—è –≤–æ–∑–º–æ–∂–Ω—ã–µ –ø–µ—Ä–µ–≥—Ä—É–∑–∫–∏ —Å–µ—Ä–≤–µ—Ä–∞ –∑–∞–ø—Ä–æ—Å–∞–º–∏, –µ—Å–ª–∏ –∏—Ö –±—É–¥–µ—Ç —Å–ª–∏—à–∫–æ–º –º–Ω–æ–≥–æ. ===
// 1.1 –û–Ω–∞ –±—É–¥–µ—Ç –ø—Ä–∏–Ω–∏–º–∞—Ç—å –¥–≤–∞ –ø–∞—Ä–∞–º–µ—Ç—Ä–∞ ¬´func¬ª, –Ω–µ–∫–∞—è —Ñ—É–Ω–∫—Ü–∏—è, –∫–æ—Ç–æ—Ä—É—é –º—ã –±—É–¥–µ–º –æ—Ç–ª–æ–∂–µ–Ω–æ –¥–µ–ª–∞—Ç—å –∏ ¬´ms¬ª - –º–∏–ª–ª–∏—Å–µ–∫—É–Ω–¥—ã, —Ç.–µ. –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –≤—Ä–µ–º–µ–Ω–∏ —Å–∫–æ–ª—å–∫–æ —É –Ω–∞—Å –±—É–¥–µ—Ç –∫–æ–ø–∏—Ç—å—Å—è –≤–≤–µ–¥—ë–Ω–Ω–∞—è –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –¥–ª—è –æ—Ç–ø—Ä–∞–≤–∫–∏ –Ω–∞ —Å–µ—Ä–≤–µ—Ä. –¢.–µ. –µ—Å–ª–∏ –≤ –ø—Ä–æ–º–µ–∂—É—Ç–∫–µ –º–µ–∂–¥—É –≤–≤–æ–¥–æ–º –æ–¥–Ω–æ–≥–æ —Å–∏–º–≤–æ–ª–∞ –¥–æ –¥—Ä—É–≥–æ–≥–æ –Ω–µ –ø—Ä–æ—à–ª–æ —É–∫–∞–∑–∞–Ω–Ω–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –º–∏–ª–ª–∏—Å–µ–∫—É–Ω–¥, —Ç–æ –∑–∞–ø—Ä–æ—Å –Ω–∞ —Å–µ—Ä–≤–µ—Ä –æ—Ç–∫–ª–∞–¥—ã–≤–∞–µ—Ç—Å—è, –ø–æ–∫–∞ –ø—Ä–æ–º–µ–∂—É—Ç–æ–∫ –º–µ–∂–¥—É –≤–≤–æ–¥–æ–º –Ω–µ —Å—Ç–∞–Ω–µ—Ç –¥–æ—Å—Ç–∞—Ç–æ—á–Ω–æ –¥–æ–ª–≥–∏–º. –ò –ø–æ—Å—Ç–∞–≤–∏–º –∑–Ω–∞—á–µ–Ω–∏–µ –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é 500.
export const debounce = (func, ms = 500) => {
  // 1.2 –î–µ–ª–∞–µ–º time, —Ç.–µ. –Ω–µ–∫—É—é –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é, –≤ –∫–æ—Ç–æ—Ä–æ–π —É –Ω–∞—Å –±—É–¥–µ—Ç –ø—Ä–æ–∏–∑–≤–æ–¥–∏—Ç—Å—è –æ—Ç—Å—á—ë—Ç –≤—Ä–µ–º–µ–Ω–∏.
  let timer;
  // 1.3 –î–∞–ª–µ–µ –Ω–∞—à–∞ —Ñ—É–Ω–∫—Ü–∏—è –ø—Ä–∏ –≤—ã–∑–æ–≤–µ –±—É–¥–µ—Ç –≤–æ–∑–≤—Ä–∞—â–∞—Ç—å –¥—Ä—É–≥—É—é —Ñ—É–Ω–∫—Ü–∏—é. –¢.–µ. –≤—ã–∑—ã–≤–∞–µ–º –º—ã —Ñ—É–Ω–∫—Ü–∏—é debounce, –Ω–æ –≤–æ–∑–≤—Ä–∞—â–∞–µ–º –Ω–µ–∫—É—é callback-—Ñ—É–Ω–∫—Ü–∏—é, –∫–æ—Ç–æ—Ä—ã–π –≤–æ–∑—å–º—ë—Ç –≤—Å–µ –ø–∞—Ä–∞–º–µ—Ç—Ä—ã —Ñ—É–Ω–∫—Ü–∏–∏.
  return (...args) => {
    // 1.4 –ü–æ—á–∏—Å—Ç–∏—Ç —Ç–∞–π–º–µ—Ä, –∫–æ—Ç–æ—Ä—ã–π –º—ã –∏—Å–ø–æ–ª—å–∑—É–µ–º –ø–æ –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä—É timer
    clearTimeout(timer);
    // 1.5 –°–æ–∑–¥–∞—ë—Ç –Ω–æ–≤—ã–π —Ç–∞–π–º–µ—Ä, –∫–æ—Ç–æ—Ä—ã–π –≤—ã–∑—ã–≤–∞–µ—Ç —Ñ—É–Ω–∫—Ü–∏—é setTimeout() —Å callback-—Ñ—É–Ω–∫—Ü–∏–µ–π func-apply(), –∫—É–¥–∞ –ø–µ—Ä–µ–¥–∞—ë–º –∫–æ–Ω—Ç–µ–∫—Å—Ç –≤—ã–∑–æ–≤–∞ this –∏ –≤—Å–µ –ø–∞—Ä–∞–º–µ—Ç—Ä—ã.
    // 1.6 –ê —á–µ—Ä–µ–∑ –∑–∞–ø—è—Ç—É—é —É–∫–∞–∂–µ–º –≤—Ä–µ–º—è —Ç–∞–π–º–∞—É—Ç–∞ –∏–∑ –Ω–∞—à–µ–≥–æ –æ–ø—Ä–µ–¥–µ–ª—ë–Ω–Ω–æ–≥–æ –∑–Ω–∞—á–µ–Ω–∏—è ¬´ms¬ª.
    timer = setTimeout(() => {func.apply(this, args)}, ms);
  };
};

// 1.7 –ò—Ç–∞–∫, —á—Ç–æ –∂–µ —Ç–∞–º –≤—ã—à–µ –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç? –ö–æ–≥–¥–∞ –º—ã –≤—ã–∑—ã–≤–∞–µ–º —Ñ—É–Ω–∫—Ü–∏—é –ø–æ–∏—Å–∫–∞ - –æ–Ω–∞ –ø–æ–ø–∞–¥–∞–µ—Ç –≤ —Ñ—É–Ω–∫—Ü–∏—é debounce(), –∑–∞–ø—É—Å–∫–∞–µ—Ç—Å—è –Ω–µ–∫–∏–π —Ç–∞–π–º–µ—Ä –Ω–∞ 500 –º–∏–ª–ª–∏—Å–µ–∫—É–Ω–¥. –ò –µ—Å–ª–∏ –≤ —Ç–µ—á–µ–Ω–∏–∏ —ç—Ç–∏—Ö 500 –º—Å –º—ã –µ—â—ë —Ä–∞–∑ –≤–≤–µ–¥—ë–º –∫–∞–∫–æ–π-—Ç–æ —Å–∏–º–≤–æ–ª, —Ç–æ —É –Ω–∞—Å —Å–Ω–æ–≤–∞ –∑–∞–ø—É—Å—Ç–∏—Ç—Å—è debounce(), –ø–æ—á–∏—Å—Ç–∏—Ç –ø—Ä–µ–¥—ã–¥—É—â–∏–π —Ç–∞–π–º–µ—Ä –∏ –∑–∞–ø—É—Å—Ç–∏—Ç –Ω–æ–≤—ã–π. –¢–∞–∫–∏–º –æ–±—Ä–∞–∑–æ–º, –µ—Å–ª–∏ –º—ã –¥–æ—Å—Ç–∞—Ç–æ—á–Ω–æ –±—ã—Å—Ç—Ä–æ –ø–µ—á–∞—Ç–∞–µ–º, —Ç–æ —É –Ω–∞—Å –Ω–µ –±—É–¥–µ—Ç –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç—å –≤—ã–∑–æ–≤ —Ñ—É–Ω–∫—Ü–∏–∏ –ø—Ä–∏ –∫–∞–∂–¥–æ–º –≤–≤–æ–¥–µ —Å–∏–º–≤–æ–ª–∞, –∫–∞–∫ —ç—Ç–æ –±—ã–ª–æ –ø—Ä–µ–∂–¥–µ.
// 1.8 –¢–µ–ø–µ—Ä—å –ø—Ä–∏—à–ª–æ –≤—Ä–µ–º—è –ø–µ—Ä–µ–π—Ç–∏ –≤ search.js –∏ –≤–æ—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å—Å—è —Ç–æ–π —Ñ—É–Ω–∫—Ü–∏–µ–π-—Ö–µ–ª–ø–µ—Ä–æ–º.