const postData = (cart) => {
  // 2.1 –î–ª—è –¥–µ–º–æ–Ω—Å—Ç—Ä–∞—Ü–∏–∏ –º–µ—Ç–æ–¥–∞ POST –∏–∑–º–µ–Ω–∏–º –Ω–∞—à fetch –Ω–∞ –Ω–æ–≤—ã–π. (–∏–∑: https://jsonplaceholder.typicode.com/guide/ "Creating a resource")
  // 2.2 –¢–µ–ø–µ—Ä—å –Ω–∞—à –º–µ—Ç–æ–¥ fetch() –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –æ–¥–∏–Ω –æ–±—è–∑–∞—Ç–µ–ª—å–Ω—ã–π –∞—Ä–≥—É–º–µ–Ω—Ç - URL —Å –∑–∞–ø—Ä–æ—Å–æ–º –Ω–∞ —Å–µ—Ä–≤–µ—Ä, –Ω–æ —Ç–∞–∫–∂–µ –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –∏ –≤—Ç–æ—Ä–æ–π –∞—Ä–≥—É–º–µ–Ω—Ç –≤ –≤–∏–¥–µ –æ–±—ä–µ–∫—Ç–∞ —Å –Ω–∞—Å—Ç—Ä–æ–π–∫–∞–º–∏ –¥–∞–Ω–Ω–æ–≥–æ –∑–∞–ø—Ä–æ—Å–∞. –ó–¥–µ—Å—å —Å–≤–æ–π—Å—Ç–≤–∞–º–∏ –º—ã –æ–ø—Ä–µ–¥–µ–ª—è–µ–º, —á—Ç–æ –∏–º–µ–Ω–Ω–æ –º—ã –±—É–¥–µ–º –æ—Ç–ø—Ä–∞–≤–ª—è—Ç—å. "method: 'POST'" - –∑–Ω–∞—á–∏—Ç –º—ã –±—É–¥–µ–º –æ—Ç–ø—Ä–∞–≤–ª—è—Ç—å –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é, –Ω–µ–∫–æ–µ —Ç–µ–ª–æ –∑–∞–ø—Ä–æ—Å–∞, —Å–≤–æ–π—Å—Ç–≤–æ "body" –∏ "headers", –∫–æ—Ç–æ—Ä—ã–µ —É–∫–∞–∑—ã–≤–∞—é—Ç—Å—è —Å–ø–µ—Ü–∏–∞–ª—å–Ω–æ –¥–ª—è —Å–µ—Ä–≤–µ—Ä–∞ JSONplaceholder, —Ç.–∫. –æ–Ω –æ–∂–∏–¥–∞–µ—Ç –∏–º–µ–Ω–Ω–æ —Ç–∞–∫–æ–µ —Ö–µ–¥–µ—Ä—ã. –ò –µ—Å–ª–∏ –∏—Ö –Ω–µ –±—É–¥–µ—Ç, –≤–æ–∑–º–æ–∂–Ω–æ –≤–æ–∑–Ω–∏–∫–Ω–µ—Ç –æ—à–∏–±–∫–∞.
  // 2.3 –í "body" –º—ã –ø–æ–º–µ—â–∞–µ–º –º–µ—Ç–æ–¥ JSON.stringify(), –∫–æ—Ç–æ—Ä—ã–π –ø—Ä–µ–æ–±—Ä–∞–∑—É–µ—Ç –æ–±—ä–µ–∫—Ç –≤ —Å—Ç—Ä–æ–∫—É –∏ –ø–æ—Ç–æ–º —ç—Ç–æ—Ç –æ–±—ä–µ–∫—Ç –≤ –≤–∏–¥–µ —Å—Ç—Ä–æ–∫–∏ –±—É–¥–µ—Ç –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω –ø–æ URL-–∑–∞–ø—Ä–æ—Å—É –≤ –ø–µ—Ä–≤–æ–º –∞—Ä–≥—É–º–µ–Ω—Ç–µ –º–µ—Ç–æ–¥–∞ fetch().
  // 6.5 (–Ω–∞—á–∞–ª–æ –≤ cart.js) –¢–µ–ø–µ—Ä—å –≤–º–µ—Å—Ç–æ –æ–±—ä–µ–∫—Ç–∞ —Å –í–µ–¥—å–º–∞–∫–æ–º 3 –ø–µ—Ä–µ–¥–∞–¥–∏–º –≤ callback-—Ñ—É–Ω–∫—Ü–∏—é –∏ –≤ body "cart". –í–æ –≤–∫–ª–∞–¥–∫–µ Dev Tools "Network" –º–æ–∂–Ω–æ —É–±–µ–¥–∏—Ç—å—Å—è, —á—Ç–æ –≤—Å—ë —Ä–∞–±–æ—Ç–∞–µ—Ç –∫–∞–∫ –Ω–∞–¥–æ.
  // return fetch('https://o-zone-fake-default-rtdb.europe-west1.firebasedatabase.app/goods.json', {
  return fetch('https://jsonplaceholder.typicode.com/posts', {
    method: 'POST',
    body: JSON.stringify(cart),
    headers: {
      'Content-type': 'application/json; charset=UTF-8',
    },
  }).then((response) => response.json()); // 2.4 –û–¥–Ω–∞–∫–æ –æ—Ç–≤–µ—Ç –Ω–∞–º —Ç–æ—á–Ω–æ —Ç–∞–∫–∂–µ –æ–±—Ä–∞–±–æ—Ç–∞—Ç—å.
};

export default postData;

// 3.1 –ò—Ç–∞–∫, –º—ã —Å–æ–∑–¥–∞–ª–∏ —É–∂–µ –¥–≤–∞ –æ—Ç–¥–µ–ª—å–Ω—ã—Ö –º–æ–¥—É–ª—è getData & postData, –∫–æ—Ç–æ—Ä—ã–µ –º–æ–∂–µ–º –≤—ã–∑—ã–≤–∞—Ç—å –∏–∑ –ª—é–±–æ–≥–æ –¥—Ä—É–≥–æ–≥–æ –º–æ–¥—É–ª—è.
// ? –ö—Ä–æ–º–µ get & post –∑–∞–ø—Ä–æ—Å–æ–≤, –µ—Å—Ç—å –µ—â—ë put, patch & delete. Delete - —É–¥–∞–ª—è–µ—Ç, put - –ø–æ–ª–Ω–æ—Å—Ç—å—é –∑–∞–º–µ–Ω—è–µ—Ç –∑–∞–ø–∏—Å—å, –∞ patch - –≤–∏–¥–æ–∏–∑–º–µ–Ω—è–µ—Ç.
