const renderGoods = (goods) => {
  const goodsWrapper = document.querySelector('.goods');
  // *2.0 == –†–µ–∞–ª–∏–∑—É–µ–º —Ñ—É–Ω–∫—Ü–∏—é –∫–æ—Ä–∑–∏–Ω—ã ==
  // 2.1 –î–ª—è —ç—Ç–æ–≥–æ –Ω–∞–º –ø–æ–Ω–∞–¥–æ–±–∏—Ç—Å—è localStorage, –∫—É–¥–∞ –º—ã –º–æ–∂–µ–º –∑–∞–Ω–æ—Å–∏—Ç—å –ª–æ–∫–∞–ª—å–Ω–æ (–≤ –∫—ç—à –±—Ä–∞—É–∑–µ—Ä–∞ —é–∑–µ—Ä–∞) –∫–∞–∫–∏–µ-—Ç–æ –¥–∞–Ω–Ω—ã–µ. –ß—Ç–æ–±—ã –∑–∞–ø–∏—Å—ã–≤–∞—Ç—å –¥–∞–Ω–Ω—ã–µ –≤ localStorage —É –Ω–∞—Å –µ—Å—Ç—å –º–µ—Ç–æ–¥ setItem(), –∫–æ—Ç–æ—Ä—ã–π –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –¥–≤–∞ –∞—Ä–≥—É–º–µ–Ω—Ç–∞: 1) –Ω–∞–∑–≤–∞–Ω–∏–µ –∫–ª—é—á–∞, –∫–æ—Ç–æ—Ä—ã–º –º—ã –Ω–∞–∑–æ–≤—ë–º –±—É–¥—É—â–∏–π –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è –∑–∞–ø–∏—Å—ã–≤–∞–µ–º—ã—Ö –¥–∞–Ω–Ω—ã—Ö; 2) –ø–µ—Ä–µ–¥–∞–¥–∏–º –º–∞—Å—Å–∏–≤ –¥–∞–Ω–Ω—ã—Ö, –Ω–æ –Ω–µ –ø—Ä–æ—Å—Ç–æ –º–∞—Å—Å–∏–≤, –∞ –æ–Ω –Ω–∞–º –Ω—É–∂–µ–Ω –≤ –≤–∏–¥–µ —Å—Ç—Ä–æ–∫–∏ –∏ –¥–ª—è —ç—Ç–æ–≥–æ –∏—Å–ø–æ–ª—å–∑—É–µ–º –º–µ—Ç–æ–¥ JSON.Stringify(). –¢–µ–ø–µ—Ä—å —É –Ω–∞—Å –∑–∞–ø–∏—à—É—Ç—Å—è –≤ localStorage –≤—Å–µ —Ç–æ–≤–∞—Ä—ã, –æ—Ç–æ–±—Ä–∞–∂–∞–µ–º—ã–µ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ.
  localStorage.setItem('goods', JSON.stringify(goods));

  goodsWrapper.innerHTML = '';
  // 2.2 –ò—Ç–∞–∫ —É –∫–∞–∂–¥–æ–≥–æ —Ç–æ–≤–∞—Ä–∞ –µ—Å—Ç—å —Å–≤–æ–π id –∏ –∏–º–µ–Ω–Ω–æ –ø–æ –Ω–µ–º—É –º—ã –±—É–¥–µ–º –æ–ø—Ä–µ–¥–µ–ª—è—Ç—å –Ω–∞ –∫–∞–∫–æ–π –∏–º–µ–Ω–Ω–æ —Ç–æ–≤–∞—Ä –º—ã –∫–ª–∏–∫–Ω—É–ª–∏. –ù—É –∞ –¥–ª—è —ç—Ç–æ–≥–æ –Ω–∞ –∫–∞–∂–¥—É—é –∫–∞—Ä—Ç–æ—á–∫—É —Ç–æ–≤–∞—Ä–∞ –Ω–∞–º –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –ø–æ–º–µ—Å—Ç–∏—Ç—å –æ–ø—Ä–µ–¥–µ–ª—ë–Ω–Ω—ã–π –¥–∞—Ç–∞-–∞—Ç—Ä–∏–±—É—Ç, –ø–æ –∫–æ—Ç–æ—Ä–æ–º—É –º—ã –±—É–¥–µ–º –æ–ø—Ä–µ–¥–µ–ª—è—Ç—å –Ω–∞ –∫–∞–∫–æ–π –∏–º–µ–Ω–Ω–æ —Ç–æ–≤–∞—Ä –º—ã –∫–ª–∏–∫–Ω—É–ª–∏. –£–∫–∞–∂–µ–º –µ–≥–æ –≤ —Ç–µ–≥–µ —Å –∫–ª–∞—Å—Å–æ–º "card". –¢–µ–ø–µ—Ä—å —É –∫–∞–∂–¥–æ–π –∏–∑ –∫–∞—Ä—Ç–æ—á–µ–∫ –µ—Å—Ç—å —É–Ω–∏–∫–∞–ª—å–Ω—ã–π –∞—Ç—Ä–∏–±—É—Ç data-id, –∫ –∫–æ—Ç–æ—Ä–æ–º—É –º—ã –º–æ–∂–µ–º –ø—Ä–∏–≤—è–∑–∞—Ç—å –ª–æ–≥–∏–∫—É. –ú—ã –º–æ–∂–µ–º –∑–∞–±—Ä–∞—Ç—å –∫–æ–Ω–∫—Ä–µ—Ç–Ω—ã–π –æ–±—ä–µ–∫—Ç –∏–∑ localStorage –∏ –ø–æ–ª–æ–∂–∏—Ç—å –µ–≥–æ –≤ –¥—Ä—É–≥–æ–º –æ–±—ä–µ–∫—Ç–µ –≤ localStorage –ø–æ–¥ –Ω–∞–∑–≤–∞–Ω–∏–µ–º "cart".
  // 2.3.1 –û–±—Ä–∞–±–æ—Ç–∞–µ–º –∫–ª–∏–∫ –ø–æ –∫–Ω–æ–ø–∫–µ "–í –∫–æ—Ä–∑–∏–Ω—É". 
  // todo 2.3.2 –î–ª—è —ç—Ç–æ —Ç–µ–æ—Ä–µ—Ç–∏—á–µ—Å–∫–∏ –º–æ–∂–Ω–æ –±—ã–ª–æ –±—ã –ø–æ–≤–µ—Å–∏—Ç—å –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è –ø–æ –∫–ª–∏–∫—É –Ω–∞ –∫–∞–∂–¥—É—é –∫–∞—Ä—Ç–æ—á–∫—É, –Ω–æ –¥–µ–ª–∞—Ç—å —ç—Ç–æ –Ω–µ –æ—á–µ–Ω—å —Ö–æ—Ä–æ—à–æ (!!!), –ø—Ä–µ–¥—Å—Ç–∞–≤–ª—è–µ—Ç–µ, –µ—Å–ª–∏ –±—ã —É –Ω–∞—Å –±—ã–ª–æ 1000 –∫–∞—Ä—Ç–æ—á–µ–∫ —Ç–æ–≤–∞—Ä–∞ –∏ –Ω–∞ –∫–∞–∂–¥–æ–π –±—ã –≤–∏—Å–µ–ª –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è. –≠—Ç–æ —Å–æ–≤–µ—Ä—à–µ–Ω–Ω–æ —Ä–∞—Å—Ç–æ—á–∏—Ç–µ–ª—å–Ω—ã–π –ø–æ–¥—Ö–æ–¥ –∫ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏—é —Ä–µ—Å—É—Ä—Å–æ–≤ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è!
  // 2.3.3 –ù–æ –±–ª–∞–≥–æ —É –Ω–∞—Å –µ—Å—Ç—å –ª—É—á—à–µ —Å–ø–æ—Å–æ–± - ¬´–¥–µ–ª–µ–≥–∏—Ä–æ–≤–∞–Ω–∏–µ¬ª. –ü–æ–≤–µ—Å–∏–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–æ–±—ã—Ç–∏—è –Ω–∞ –≤–µ—Å—å –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä, –≥–¥–µ –Ω–∞—Ö–æ–¥—è—Ç—Å—è –≤—Å–µ –∫–∞—Ä—Ç–æ—á–∫–∏ —Ç–æ–≤–∞—Ä–∞ —Å –∫–ª–∞—Å—Å–æ–º ".goods", –∞ –ø–æ—Ç–æ–º –ø—Ä–æ–≤–µ—Ä–∏–º –ø–æ –∫–∞–∫–æ–π –∏–º–µ–Ω–Ω–æ –∫–∞—Ä—Ç–æ—á–∫–∏ –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç –∫–ª–∏–∫. –¢–∞–∫–∏–º –æ–±—Ä–∞–∑–æ–º –º—ã –Ω–µ –∑–∞–≤–∏—Å–∏–º –±–æ–ª—å—à–µ –æ—Ç –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ –∫–∞—Ä—Ç–æ—á–µ–∫ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—ã –∏ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ —É –Ω–∞—Å –±—É–¥–µ—Ç –≤—Å–µ–≥–æ –ª–∏—à—å –æ–¥–∏–Ω.
  // 2.3.4 –ü–µ—Ä–µ–π–¥—ë–º –≤ cart.js –∏ –ø–æ–ª—É—á–∏–º –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä ".goods"...
  goods.forEach(goodsItem => {
    goodsWrapper.insertAdjacentHTML(
      'beforeend',
      `
        <div class="col-12 col-md-6 col-lg-4 col-xl-3">
          <div class="card" data-id="${goodsItem.id}">
            ${goodsItem.sale ? '<div class="card-sale">üî•Hot Saleüî•</div>' : ''}
            <div class="card-img-wrapper">
              <span class="card-img-top" style="background-image: url('${
                goodsItem.img
              }')"></span>
            </div>
            <div class="card-body justify-content-between">
              <div class="card-price">${goodsItem.price} ‚ÇΩ</div>
              <h5 class="card-title">${goodsItem.title}</h5>
              <button class="btn btn-primary">–í –∫–æ—Ä–∑–∏–Ω—É</button>
            </div>
          </div>
        </div>
      `
    );
  });
};

export default renderGoods;
